:root {
  --main-bg-color: #181818;
  --header-bg-color: #202020;
  --main-text-color: #ffffff;
}

*::-webkit-scrollbar {
  width: .6em;
  
}
*::-webkit-scrollbar,
*::-webkit-scrollbar-thumb {
  overflow:visible;
  border-radius: 4px;
  
}
*::-webkit-scrollbar-thumb {
  background: rgba(0,0,0,.6); 
}


html
{
scroll-behavior:smooth
}
html{
scroll-behavior:smooth
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: var(--main-text-color);
  font-family: 'Montserrat';
}
a {
  text-decoration: none;
}

body {
  background-color: var(--main-bg-color);
}

#root {
  /* padding-top: 40px */
  /* height: 3000px; */
}
#root::before {
  content: '';
  width: 100%;
  height: 2px;
  min-height: 1px;
  background-color: #00A7C5;
  position: absolute;
  transition: .6s;
  animation: loading 1s linear;
}

.background-parallax {
  height: 100vh;
  background-image: url('/header-op.png');
}

header {
  padding-top: 60px;
  min-height: 60px;
  position: relative;
  z-index: 999;
}

.container {
  margin: 0 30px;
}

header .container {
  display: flex;
  align-items: center;
  min-height: 60px;
}

.hd-left {
  margin-right: auto;
}

.hd-logo {
  height: 100px;
}

.hd-right {
  display: flex;
}
.hd-right > a {
  padding: 6px 8px;
  margin-right: 4px;
  letter-spacing: 2px;
  font-size: 13px;
  font-weight: 400;
  transition: color .2s ease;
  color: #aeb2b7;
  position: relative;
}
.hd-right:hover {

}
.hd-right > a::before {
  content: '';
  width: 0%;
  height: 2px;
  background-color: #2FFFAD;
  position: absolute;
  bottom: 0;
  transition: all .2s;
  z-index: 11;
}

.hd-right > a::after {
  content: attr(data-text);
  position: fixed;
  bottom: 50%;
  left: 40%;
  transform: translate(-50%, 50%);
  font-size: 8em;
  letter-spacing: 500px;
  font-weight: 900;
  transition: all .4s;
  opacity: 0;
  visibility: hidden;
}
.hd-right > a:hover::after {
  letter-spacing: 3px;
  left: 50%;
  opacity: 1;
  visibility: visible;
}

